@import './mixins.scss';
@import './variables.scss';
body * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
	margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  background-color: $white;
  color: #909090;
}

li {
  list-style-type: none;
  font-size: 16px;
  letter-spacing: 0.5px;
}
a:active,
a {
  text-decoration: none;
  color: #000;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

p {
  font-style: normal;
  font-weight: normal;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.4px;
}

/* Main page */
h1 {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.8px;
}

h2 {
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 1.5px;
}

.main-page {
  display: flex;
  flex-grow: 1;
  align-items: center;
}

.left-article {
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: center;
  div {
    display: flex;
    align-items: center;
    padding-bottom: 50px;
    h2 {
      color: #000;
      font-weight: bold;
      padding-left: 8px;
    }
  }
  .site-title {
    flex-direction: column;
    max-width: 368px;
    text-align: center;
    padding-bottom: 52px;
    h1 {
      color: #000;
      padding-bottom: 20px;
    }
  }
  button {
    @include main-button(158px, 52px);
  }
}

.right-article {
  display: flex;
  position: relative;
  justify-content: center;
  width: 50%;
  overflow: hidden;
  max-height: 756px;
  nav {
    position: absolute;
    width: 100%;
    top: 0;
    ul {
      display: flex;
      flex-direction: row;
      left: 0;
      right: 0;
      justify-content: space-between;
      padding: 0 80px;
      min-height: 132px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
      li {
        padding-top: 22px;
        color: $gray;
      }
    }
  }
}
